<template>
     <div class="container">
        <div class="main__info" v-if="posts">
            <div class="main__info-item" v-for="post in posts" :key="post.id">
                <h2>{{ post.id }}</h2>
                <p>{{ post.title }}</p>
                <p>{{ post.body }}</p>      
            </div>
        </div>
        <div v-else>
            Loading....
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                
            }
        },
        created() {
            this.getPosts()
        },
        computed: {
            posts() {
                return this.$store.state.posts
            }
        },
        methods: {
            getPosts() {
                this.$store.dispatch('getPosts')
            }
          
        },
    }
</script>

